import React from 'react';
import { Canvas } from '@react-three/fiber';
import { OrbitControls, Environment } from '@react-three/drei';
import Humanfootbasemesh from '@/34/components/Humanfootbasemesh';
/**
 * Example page demonstrating how to use the Humanfootbasemesh component
 */
export default function HumanfootbasemeshPage() {
  return (
    <div className="w-full h-screen">
      <Canvas 
        camera={{ position: [0, 0, 5], fov: 50 }}
        gl={{ 
          powerPreference: "high-performance",
          antialias: true,
          alpha: true
        }}
      >
        <ambientLight intensity={0.5} />
        <pointLight position={[10, 10, 10]} />
        <Humanfootbasemesh />
        <OrbitControls 
          enableZoom 
          enablePan 
          enableRotate 
          minDistance={0.1}
          maxDistance={100}
        />
        <Environment preset="studio" />
      </Canvas>
    </div>
  );
}